<template>
  <div id="app">
    <Header />
    <vk-grid class="grid uk-margin-top uk-margin-bottom uk-margin-left uk-margin-right">
      <div class="uk-width-large" v-for="data in myJson" v-bind:key="data">
        <WorkerInfo
          :title="data.title"
          :host="data.host"
          :wallet="data.wallet"
          :balanceKey="data.balanceKey"
          :paidKey="data.paidKey"
        ></WorkerInfo>
      </div>
      <div class="uk-width-large">
        <AddWorkerInfo />
      </div>
      <!-- <vk-grid>
        <div v-for="data in myBlackminers" v-bind:key="data">
          <div class="uk-width-xlarge">
            <BlackminerInfo :host="data.host" />
          </div>
        </div>
      </vk-grid>-->
    </vk-grid>
  </div>
</template>

<script>
import configJson from "./assets/json/config.json";
import blackminerConfigJson from "./assets/json/blackminer.json";
import Header from "./components/layout/Header.vue";
import BlackminerInfo from "./components/BlackminerInfo";
import WorkerInfo from "./components/WorkerInfo";
import AddWorkerInfo from "./components/AddWorkerInfo";

export default {
  name: "App",
  components: {
    Header,
    BlackminerInfo,
    WorkerInfo,
    AddWorkerInfo
  },
  data() {
    return {
      myJson: configJson,
      myBlackminers: blackminerConfigJson
    };
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: whitesmoke;
  padding-bottom: 100%;
  font-size: 80%;
}
</style>



