<template>
  <div id="worker-info">
    <vk-card>
      <vk-card-title>{{title}}</vk-card-title>
      <table class="my-table uk-table uk-table-small">
        <tbody>
          <tr>
            <td>Address</td>
            <td v-if="this.connecting == true" class="uk-text-right">
              <vk-spinner ratio="0.5"></vk-spinner>
            </td>
            <td v-else class="uk-text-right">{{ minerdata.address }}</td>
          </tr>
          <tr>
            <td>Balance</td>
            <td v-if="this.connecting == true" class="uk-text-right">
              <vk-spinner ratio="0.5"></vk-spinner>
            </td>
            <td v-else class="uk-text-right">{{ minerdata.balance }}</td>
          </tr>
          <tr>
            <td>Paid</td>
            <td v-if="this.connecting == true" class="uk-text-right">
              <vk-spinner ratio="0.5"></vk-spinner>
            </td>
            <td v-else class="uk-text-right">{{ minerdata.paid }}</td>
          </tr>
        </tbody>
      </table>
      <br />
      <vk-button class="uk-align-right" v-on:click="refresh" type="primary">REFRESH</vk-button>
      <br />
      <table class="uk-table uk-table-striped uk-table uk-table-small">
        <tbody>
          <tr v-for="(value, key) in jsonResult" v-bind:key="key">
            <td>{{key}}</td>
            <td>{{value}}</td>
          </tr>
        </tbody>
      </table>
    </vk-card>
  </div>
</template>
 
<script>
import axios from "axios";
export default {
  name: "home",
  props: {
    title: String,
    host: String,
    wallet: String,
    balanceKey: String,
    paidKey: String
  },
  mounted() {
    this.refresh();
  },
  data() {
    return {
      jsonResult: JSON,
      minerdata: {
        address: "",
        balance: "",
        paid: ""
      },
      connecting: true
    };
  },
  methods: {
    clearInfo: function() {
      this.minerdata.address = "";
      this.minerdata.balance = "";
      this.minerdata.paid = "";
    },
    refresh: function() {
      this.clearInfo();
      this.connecting = true;
      axios
        .get("https://cors-anywhere.herokuapp.com/" + this.host + this.wallet)
        .then(res => {
          console.log(res);
          this.jsonResult = res.data;
          this.minerdata.address = this.wallet;
          this.minerdata.balance = res.data[this.balanceKey];
          this.minerdata.paid = res.data[this.paidKey];
          this.connecting = false;
        });
    }
  },
  computed: {}
};
</script>
 
<style scoped>
#worker-info {
  text-align: left;
}
.my-table {
  font-weight: bold;
}
</style>


