<template>
  <div id="blackminer-info">
    <vk-card>
      <vk-card-title>Blackminer Mini</vk-card-title>
      <input class="uk-input uk-form-width-medium" ref="hostInput" :value="host" type="text" />
      <vk-button v-on:click="getdata" type="primary">GET</vk-button>
      <!-- <img src="../assets/circle_logo.png" width="50" height="50"> -->
      <div id="responsediv">
        <br />
        {{ response.message }}
      </div>
    </vk-card>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "BlackminerInfo",
  props: {
    title: String,
    host: String
  },
  data: function() {
    return {
      response: {
        message: "Not connected",
        success: false
      }
    };
  },
  methods: {
    getdata: function() {
      axios
        .get(
          `${"https://cors-anywhere.herokuapp.com/"}http://` +
            this.$refs.hostInput.value +
            "/cgi-bin/minerStatus.cgi", {
              'responseType': 'json'
            }
        )
        .then(res => {
          console.log(res);
          this.response.message = res;
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#blackminer-info {
  text-align: left;
  padding: 0px;
  margin: 0px;
}
</style>



