<template>
  <div id="add-worker-info">
    <vk-card padding="large">
      <vk-button class="uk-align-center" v-on:click="show = true" type="link">
        <vk-icon icon="plus" ratio="3" />
      </vk-button>
    </vk-card>
    <vk-modal :show.sync="show" overflow-auto>
      <vk-modal-close @click="show = false"></vk-modal-close>
      <vk-modal-title slot="header">Add worker info</vk-modal-title>
      <input class="uk-input uk-margin" type="text" placeholder="title" />
      <input class="uk-input uk-margin" type="text" placeholder="host" />
      <input class="uk-input uk-margin" type="text" placeholder="wallet" />
      <input class="uk-input uk-margin" type="text" placeholder="balanceKey" />
      <input class="uk-input uk-margin" type="text" placeholder="paidKey" />
      <div slot="footer" class="uk-text-right">
        <vk-button class="uk-margin-small-right" @click="show = false">Cancel</vk-button>
        <vk-button @click="this.saveWorker" type="primary">Save</vk-button>
      </div>
    </vk-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false
    };
  },
  methods: {
    saveWorker: function() {
      this.show = false;
      console.log("Worker saved.");
    }
  }
};
</script>

<style scoped>
</style>



